<template>
  <div class='for'>
      <h1>{{title}}</h1>
      <h3>{{DGphone}}</h3>
      <div class='phone'>
          <p class='phoneInfo' v-for='item in phone'>{{item}}</p>
          <button class='but' @click="allocatePhone()">拨打电话</button>   
      </div>
      <div class='tel' ref="tels">
          <button class='X' @click='X'>关闭</button>          
          <a class ='phoneIndex'href='tel:18668993180'>李孝庆 18668993180</a>
          <a class ='phoneIndex'href='tel:13173005203'>李经辉 13173005203</a>
      </div>
  </div>
</template>
<style lang="stylus" scoped>
.for
 padding 5%
 .phoneInfo
  line-height 0.5333rem
.but
 border-top-style none
 border-right-style none
 border-bottom-style none
 border-left-style none
 color #fff
 background rgb(15, 136, 235)
 width 2.666rem
 height .866rem
 border-radius .2666rem
.tel
 width 100%
 height 4rem
 border-radius .21333rem .21333rem 0 0 
 position fixed
 z-index 999
 bottom -4.866rem
 transition all .15s
 left 0
 .X
  height 30%
  width 100%
  border-top-style none
  border-right-style none
  border-bottom-style none
  border-left-style none
  background #CE3C39
  color #fff
  border-radius .1333rem .1333rem 0 0
 .phoneIndex
  display block
  width 100%
  height 35%
  display flex
  justify-content center
  align-items center
  color #fff
  font-size 0.42666rem
  background #1AAD19
  border-bottom 1px solid #fff
</style>
<script>
export default {
  name:'for',
  data(){
      return {
          title:'联系我们',
          DGphone:'订购电话：',
          phone:[
              '李孝庆 18668993180  lixiaoqing@cmdmedia.cn',
              '李经辉 13173005203  hmhyfz0014@126.com'
          ]
      }
  },
  methods:{
      allocatePhone(){
          const tel=this.$refs.tels;
          tel.style.bottom='0px'
      },
      X(){
          const tel=this.$refs.tels;
          tel.style.bottom='-4.1rem'
      }
  }
}
</script>



