<template>
<div class='two'>
        <div class='foll'>
          <h1 class='title'>{{title}}</h1>
          <div>
               <p class='info' v-for='item in info'> <span class='thereJ'>▶</span><span class='unit'>{{item.unit}}</span><span class='chapter'>{{item.chapter}}</span><span class='in'>{{item.in}}</span> </p>
          </div>
        </div>
</div>
</template>
<style lang="stylus" scoped>
.two
 padding 5%
 .foll
  .title
   line-height 2.0666rem
   padding-right 0.4rem
  .info
   line-height 1.0666rem
   padding-left 0.5rem
  .thereJ
   color #76EE00
   border-radius 100%
   display inline-block
   padding-right .3rem
  .chapter
   display inline-block
   float right
   padding-right 0.4rem
  .in
   float right
   color #999
   display inline-block
   padding 0 0.3rem 0 0.3rem
</style>
<script>
export default {
  name:'two',
  data(){
      return {
          title:'书籍目录',
          info:[
              {unit:'医疗设备采购风险管理',in:'1-85',chapter:'第一章'},
              {unit:'医疗设备采购管理流程案例解读',in:'86-145',chapter:'第二章'},
              {unit:'高值耗材采购管理实务',in:'146-195',chapter:'第三章'},
              {unit:'医学设备参数与性能评价',in:'196-421',chapter:'第四章'},
              {unit:'医院信息化管理软件',in:'422-455',chapter:'第五章'}
          ]
      }
  }
}
</script>



